*{
    box-sizing: border-box;
}
*:before,
*:after {
    box-sizing: border-box;
}
* {
    margin: 0;
    padding: 0;
}
ul, ol {
    list-style: none;
}
a {
    color: inherit;
    text-decoration: none;
}
img {
    max-width: 100%;
    max-height: 100%;
}
body {
    display: flex;
    flex-direction: column;
    background: #dddddd;
}
.globalHeader {
    width: 100%;
    height: 40vw;
    display: flex;
    flex-direction: column;
}
.webTop {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
}
.webTop .icon1{
    border-style: none;
    height:10vw;
    width:10vw;
    margin:1vw;
    /*cursor: pointer;*/
}
/*移动端可以没有cursor: pointer;*/
.webTop .webIcon{
    font-size: 7vw;
    font-weight: 1000;
}
.webTheme{
    display: flex;
    justify-content: flex-end;
}
.webTheme .icon2{
    border-style: none;
    margin: 1vw 3vw 0 0;
    height:6vw;
    width: 6vw;
    /*cursor: pointer;*/
}
.searchEngine {
    display: flex;
    padding-left: 10vw;
}
.searchEngine .searchPortal1{
    margin-left: 5vw;
    height: 6vw;
}
.searchEngine .searchPortal2{
    margin-left: 5vw;
    height: 6vw;
}
.searchEngine .searchPortal3{
    margin-left: 5vw;
    height: 6vw;
}
.searchForm {
    width:90vw;
    border: 2px solid #00B4CC;
    border-radius: 25px;
    margin: 0 auto;
    padding: 1vw 4vw;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.searchForm .searchInput {
    width:65vw ;
    height: 8vw;
    font-size: 16px;
    border-style: none;
    outline:none;
}
.searchForm .searchButton {
    align-items: center;
    /*cursor: pointer;*/
    font-size: 8vw;
    background: #ffffff;
    border-style: none;
}
.globalMain {
    /*max-width: 900px;*/
    width: 100%;
}
.siteList {
    margin: 50px auto ;
    width: 84vw;
    display: flex;
    flex-wrap: wrap;
   align-content: flex-start;
}

.siteList .site {
    width: 28vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    /*cursor: pointer;*/
    position: relative;
}
.siteList .site .logo {
    width: 20vw;
    height: 20vw;
    border-radius: 50%;
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size:10vw;
}
.siteList .site .link {
    font-size: 4vw;
    margin: 1vw 0 4vw 0;
}
.siteList .site .close {
    border: 2px solid red;
    position: absolute;
    right: 10px;
    top: 10px;
    display: none;
    cursor: default;
}
.siteList .site:hover .close {
    display: block;
}
.siteList .addButton {
    width: 28vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.siteList .addButton .iconWrapper {
    width: 20vw;
    height: 20vw;
    border-radius: 50%;
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.siteList .addButton .text {
    font-size: 4vw;
    margin: 1vw 0 4vw 0;
}
.siteList .addButton .iconWrapper .icon {
    width: 80%;
    height: 80%;
    border-style: none;
}
.globalFooter{
    border:2px solid green;
    background: #355095;
    width:100%;
    height:50px;
}